.art-content-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100%;
    width: 100%;
}

.left-art-wrapper {
    width: 100%;
    height: auto;
}

.video-grid-container {
    height: 100%;
    width: 100%;
    padding: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.video-container {
    height: 100%;
    width: 100%;
    position: relative;
    max-width: 1200px;
}
.auto-video {
    width: 100%;
    height: 100%;
    border-radius: 10px;
}

.grid-container {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
}

.grid-container h3 {
    font-family: oakes-grotesk;
}

.grid {
    height: 100%;
    width: 100%;
    padding-top: 4px;
}

.grid-item-wrapper {
    height: 50%;
    padding: 0;
}

.grid-item-border1 {
    border-right: 1px solid rgba(255, 255, 255, 0.75);
}

.grid-item-border3 {
    border-left: 1px solid rgba(255, 255, 255, 0.75);
}

.grid-item-border4 {
    border-top: 1px solid rgba(255, 255, 255, 0.75);
}

.grid-item-border5 {
    border-top: 1px solid rgba(255, 255, 255, 0.75);
    border-left: 1px solid rgba(255, 255, 255, 0.75);
}

.grid-item-paper {
    height: 100%;
    opacity: 0; 
    padding: 0;
}

.grid-img {
    width: 100%;
    height: 100%;
}

.grid-item-paper:hover {
    cursor: pointer;
    opacity: 1;
}