.home-container {
    background-color: #f5f5f5;
    height: 100vh;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: -1;
}

.home-img-wrapper {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    width: auto;
    height: 75%;
    z-index: 2;
}

.home-img {
    width: 100%;
    height: 100%;
    opacity: 25%;
    z-index: 2;
}

.home-text-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: auto;
}

.home-header-1 {
    font-family: hackney;
    font-size: 15rem;
    padding: 50px;
    z-index: 3;
}

.home-header-2 {
    font-family: hackney;
    font-size: 10rem;
    padding-top: 250px;
    z-index: 3;
}

.home-header-3 {
    font-family: hackney;
    font-size: 15rem;
    padding: 50px;
    z-index: 3;
}


@media screen and (max-width: 960px) {
    .home-container {
        padding-right: 30px;
        padding-left: 30px;
    }

    .home-text-wrapper {
        display: inline-block;
        width: 100%;
    }

    .home-header-1 {
        padding-bottom: 0;
    }
   
    .home-header-2 {
        padding: 0;
        margin: -50px 0 0 0;
        float: right;
    }

}
