.interests-header-container {
    background-color: #e9e9e9;
    height: 100vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.interests-background {
    height: 300px;
    width: auto;
    position: fixed;
    bottom: 30%;
    right: 10%;
    z-index: 0;
}

.interests-text-wrapper {
    height: auto;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.other-works-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
}

.other {
    font-family: hackney;
    font-size: 10rem;
    align-self: flex-start;
}

.works {
    font-family: hackney;
    font-size: 15rem;
    align-self: flex-start;
}

.interests-container {
    height: auto;
    width: 100vw;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #f5f5f5;
    z-index: 1;
    padding: 2rem;
}

.interests-content-wrapper {
    display: flex;
    align-items: flex-start;
    justify-content: center;
    height: 100%;
    width: 100%;
}

.interests-header {
    font-family: hackney;
    font-size: 10rem;
}

.interests-subHeader {
    font-family: oakes-grotesk;
    font-size: 2rem;
}

.interests-description {
    font-family: oakes-grotesk;
    font-size: 1.4rem;
}

.extra-space {
    background-color: #f5f5f5;
    height: 5rem;
    width: 100vw;
}